<template>
  <div style="flex: 1">
    <select v-model="$store.state.month">
      <option v-for="inx in 12" :key="inx">{{inx}}</option>
    </select>
    <div v-if="getchartData.rows.length > 0">
        <ve-pie :data="getchartData" ></ve-pie>
    </div>
    <div v-else>
        <span>噢噢 還沒有明細喔(´Ａ｀。)</span>
    </div>

  </div>
</template>
<script>
export default {
  name: "costchart",
  computed: {
      getchartData: function() {
          return this.$store.getters.getCostObj
      }
  }
};
</script>
<style>
</style>


