<template>
  <div
    style="flex: 1; display: flex; flex-direction: column; align-items: start"
  >
    <div>
      <div>目前月份：{{ getchartMonth }}月</div>
    </div>
    <div v-for="(item, index) in obj" :key="index">
      <span>{{ item.type }}</span>
      <input
        type="text"
        style="width: 45px"
        v-model="item.cost"
        @change="inputCost"
      />
    </div>
  </div>
</template>
<script>
export default {
  name: "costlist",
  computed: {
    getchartMonth: function () {
      this.month = this.$store.state.month;
      return this.$store.state.month;
    },
  },
  watch: {
    month: function () {
      this.obj = [];
      let sts = false;
      this.costarr.forEach((item) => {
        if (item.month == this.month) {
          this.obj = item.item;
          sts = true;
        }
      });
      if (!sts) {
        this.titleType.forEach((item) => {
          this.obj.push({ type: item, cost: 0 });
        });
      }
    },
  },
  data() {
    return {
      titleType: [
        "education",
        "food",
        "insurance",
        "travel",
        "enterance",
        "social",
      ],
      obj: [],
      month: "",
      costarr: [],
    };
  },
  methods: {
    inputCost() {
      this.$store.state.mingCost.push({
        month: this.month,
        item: this.obj,
      });
    },
  },
  mounted() {
    this.costarr = [...this.$store.state.mingCost];
  },
};
</script>
<style>
</style>


